import React, { Component } from 'react';
import { WebView } from 'react-native-webview';
import { ActivityIndicator, StyleSheet, Button, TouchableOpacity, Image } from 'react-native';
import { MFPaymentRequest, MFPaymentStatusRequest } from './index.js';
import { CALLBACK_URL, ERROR_URL } from './index.js';
import { MFSettings, Response} from './index.js';
import { YellowBox } from 'react-native';

YellowBox.ignoreWarnings([
  'Non-serializable values were found in the navigation state',
]);

export default class MFWebView extends Component {
  
  
  constructor(props) {
    super(props);
  }
  componentDidUpdate(prevProps) {
    this.myWebView.reload()
  }

  //region HELPERS METHODS
  LoadingIndicatorView() {
    return <ActivityIndicator color={MFSettings.sharedInstance.getTheme().navigationTintColor} size='large' style={styles.ActivityIndicatorStyle} />
  }
  parseURLParams(name, url) {
    if (!url) url = window.location.href;
    name = name.replace(/[\[\]]/g, '\\$&');
    var regex = new RegExp('[?&]' + name + '(=([^&#]*)|&|#|$)'),
      results = regex.exec(url);
    if (!results) return null;
    if (!results[2]) return '';
    return decodeURIComponent(results[2].replace(/\+/g, ' '));
  }
  //endregion

  //region CALLBACKS
  onNavigationStateChange = (navigationState) => {
    const url = navigationState.url;

    const { route } = this.props
    const { navigation } = this.props
    const { apiLanguage } = route.params
    const { postExecuteCallback } = route.params
    const { cardInfoResponse } = route.params
    // alert('onNavigationStateChange')
    if (url.includes(CALLBACK_URL) || url.includes(ERROR_URL)) {
      if (url.includes('paymentId')) {
        let paymentId = this.parseURLParams('paymentId', url);
        var paymentStatusRequest = new MFPaymentStatusRequest(paymentId, "PaymentId")
        MFPaymentRequest.sharedInstance.getPaymentStatus(paymentStatusRequest, apiLanguage, (response: Response) => {
          var errorMessage = response.errorMessage(false, false)
          if (errorMessage.length !== 0) {
            response.setError('Error Message', errorMessage)
            postExecuteCallback(response);
          } else {

            if (cardInfoResponse !== undefined) {
              var directPaymentResponse = new Response()
              var directPaymentJson = {
                getPaymentStatusResponse: response.getBodyJson().Data,
                cardInfoResponse: cardInfoResponse
              }
              directPaymentResponse.setBody(JSON.stringify(directPaymentJson))
              postExecuteCallback(directPaymentResponse)
            } else {
              postExecuteCallback(response)
            }
          }
          navigation.goBack()
        })
      }
    }
  };
  //endregion

  //region navigationOptions
  static navigationOptions = ({ navigation, route }) => {
    return {
      gestureEnabled: false,
      title: MFSettings.sharedInstance.getTheme().navigationTitle,
      headerStyle: {
        backgroundColor: MFSettings.sharedInstance.getTheme().navigationBarTintColor,
      },
      headerTintColor: MFSettings.sharedInstance.getTheme().navigationTintColor,
      headerRight: () => (
        <TouchableOpacity onPress={()=> navigation.setParams({ refresh: 1 })}>
          <Image style={[styles.ImageClass, {tintColor: MFSettings.sharedInstance.getTheme().navigationTintColor}]} source={require('./assets/refresh.png')} />
        </TouchableOpacity>
      ),
      headerLeft: () => (
        <Button
          onPress={() => {
            const { postExecuteCallback } = route.params
            let response = new Response()
            response.setError('Error Message', 'User clicked cancel button')
            postExecuteCallback(response)
            navigation.goBack()
          }}
          title={MFSettings.sharedInstance.getTheme().cancelButtonTitle}
          color={MFSettings.sharedInstance.getTheme().navigationTintColor}
        />
      ),
    };
  };
  //endregion
  
  
  render() {
    const { route } = this.props
    const { link } = route.params

    return (
      <WebView
        source={{ uri: link }}
        renderLoading={this.LoadingIndicatorView}
        startInLoadingState={true}
        ref={(ref) => this.myWebView = ref}
        onNavigationStateChange={this.onNavigationStateChange}
      />
    )
  }
}

const styles = StyleSheet.create({
  ActivityIndicatorStyle: {
    flex: 1,
    justifyContent: 'center',
    top: 0, right: 0, left: 0, bottom: 0,
    position: 'absolute',
  },
  ImageClass:
  {
    // Setting up image width.
    width: 25,

    // Setting up image height.
    height: 25,
    margin: 10
  }
})