
export class MFSettings {
    static sharedInstance = MFSettings.sharedInstance == null ? new MFSettings() : MFSettings.sharedInstance
    configure(baseURL, token) {
        if (baseURL.charAt(baseURL.length-1) !== "/") {
            this.baseURL = baseURL + '/';
        } else {
            this.baseURL = baseURL;
        }
      this.token = token
    }
  
    getBaseURL() {
      return  this.baseURL + 'v2/';
    }
    getToken() {
      return this.token
    }
    setTheme(theme) {
      this.theme = theme
    }
    getTheme() {
      if(this.theme === undefined) {
        return new MFTheme('red', 'gray', 'Payment', 'Cancel')
      }
      return this.theme
    }
    
  }

  export function MFTheme(navigationTintColor, navigationBarTintColor, navigationTitle, cancelButtonTitle) {
    this.navigationTintColor = navigationTintColor
    this.navigationBarTintColor = navigationBarTintColor
    this.navigationTitle = navigationTitle
    this.cancelButtonTitle = cancelButtonTitle
}