import { CALLBACK_URL, ERROR_URL } from './index.js';

export function MFPaymentStatusRequest(key, keyType) {
  this.key = key
  this.keyType = keyType
}

export function MFInitiatePayment(invoiceAmount, currencyIso) {
  this.invoiceAmount = invoiceAmount
  this.currencyIso = currencyIso
}
export function MFExecutePaymentRequest(invoiceValue, paymentMethod, callBackUrl, errorUrl) {
  this.invoiceValue = invoiceValue
  this.paymentMethod = paymentMethod
  if (callBackUrl === undefined) {
    this.callBackUrl = CALLBACK_URL
  } else {
    this.callBackUrl = callBackUrl
  }
  if (errorUrl === undefined) {
    this.errorUrl = ERROR_URL
  } else {
    this.errorUrl = errorUrl
  }

  this.customerName = ''
  this.customerCivilId = ''
  this.customerReference = ''
  this.customerEmail = ''
  this.customerMobile = ''
  this.invoiceItems = []
  this.language = 'en'
  this.expiryDate = null
  this.customerAddress = new MFCustomerAddress('', '', '', '', '')
  this.userDefinedField = ''
  this.displayCurrencyIso = 'KWD'
  this.mobileCountryCode = '+965'
  this.supplierCode = 0
  this.supplierValue = 0
}

export function MFProduct(name, unitPrice, quantity) {
  this.name = name
  this.unitPrice = unitPrice
  this.quantity = quantity
}

export function MFCustomerAddress(block, street, houseBuildingNo, address, addressInstructions) {
  this.block = block
  this.street = street
  this.houseBuildingNo = houseBuildingNo
  this.address = address
  this.addressInstructions = addressInstructions
}

export function MFCardInfo(
  cardNumber: String,
  cardExpiryMonth: String,
  cardExpiryYear: String,
  cardSecurityCode: String,
  cardHolderName: String,
  paymentType: String,
  saveToken: Boolean) {
  this.paymentType = paymentType
  this.card = new MFCard(cardNumber, cardExpiryMonth, cardExpiryYear, cardSecurityCode, cardHolderName)
  this.saveToken = saveToken
  this.bypass = true
}

function MFCard(number, expiryMonth, expiryYear, securityCode, cardHolderName) {
  this.number = number
  this.expiryMonth = expiryMonth
  this.expiryYear = expiryYear
  this.securityCode = securityCode
  this.cardHolderName = cardHolderName
}

export function MFSendPaymentRequest(invoiceValue, notificationOption, customerName) {
  this.invoiceValue = invoiceValue
  this.customerName = customerName
  this.notificationOption = notificationOption
  this.mobileCountryIsoCode = '+965'
  this.displayCurrencyIso = 'KWD'
  this.customerCivilId = ''
  this.customerReference = ''
  this.customerEmail = ''
  this.customerMobile = ''
  this.invoiceItems = []
  this.language = 'en'
  this.expiryDate = null
  this.errorUrl = ''
  this.customerAddress = new MFCustomerAddress('', '', '', '', '')
  this.callBackUrl = ''
  this.userDefinedField = ''
  this.supplierCode = 0
  this.supplierValue = 0
}